import React, { useEffect, useRef } from 'react'
import bubbles from '../img/icons/bubbles.png'
import swimLogo from '../img/icons/swim-circle.svg'
import swimWhite from '../img/icons/swim-2.svg'
import gsap from 'gsap'

const BubblesBlock = () => {
    const bubblesBlockRef = useRef()
    const strLineRef = useRef()
    const swimLogoRef = useRef()
    const bubblesRef = useRef()
    
    useEffect(() => {
        const tl = gsap.timeline({
            scrollTrigger: {
                trigger: bubblesBlockRef.current,
                start: 'top top',
                end: '+=3000',
                scrub: 1,
                pin: true
            }
        })

        tl.to(strLineRef.current, {
            xPercent: -100
        }, 0)

        tl.to(swimLogoRef.current, {
            scale: 1
        }, 0)

        tl.to(bubblesRef.current, {
            yPercent: -80
        }, 0)

    }, [])

    return (
        <div ref={bubblesBlockRef} className="bubbles-block">
            <div className="bubbles-block__wrapper">
                <div className="bubbles-block__swim-logo">
                    <img ref={swimLogoRef}  src={swimLogo} alt="Swim System" />
                </div>
                <img ref={bubblesRef} className="bubbles-block__swim-bubbles" src={bubbles} alt="Swim System" />
                <div ref={strLineRef} className="bubbles-block__string-line">
                    <span>swim <img src={swimWhite} alt="Swim System" /> system - swim <img src={swimWhite} alt="Swim System" /> system</span>
                </div>
            </div>
        </div>
    )
}

export default BubblesBlock
